@keyframes fadeInZoomOut {
    0% {
        opacity: 0;
        transform: scale(0.5);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

.car-item {
    padding: 15px;
    animation: fadeInZoomOut 0.5s ease-in-out forwards; 
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; 
}

.car-item:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); 
}

.car-image {
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
}

.car-title {
    font-size: 1.2rem;
    margin-bottom: 10px;
    font-weight: 600;
}

.car-info .btn {
    margin-top: 10px;
    width: 100%;
    font-size: 1rem;
    padding: 10px;
}

.starting-text {
    font-size: 1rem;
    color: #333;
    margin-bottom: 10px;
}

.main-section {
    background-color: #D6CCB6;
    padding: 20px 0;
}

@media (max-width: 768px) {
    .car-title {
        font-size: 1rem;
    }

    .starting-text {
        font-size: 0.9rem;
    }

    .car-info .btn {
        font-size: 0.9rem;
    }
}

@media (max-width: 576px) {
    .car-item {
        padding: 10px;
    }

    .car-image {
        width: 100%;
        height: auto;
    }

    .car-title {
        font-size: 0.9rem;
    }

    .starting-text {
        font-size: 0.8rem;
    }

    .car-info .btn {
        width: 100%;
        font-size: 0.9rem;
        padding: 8px;
    }
}
