.navbar {
    background-color: #fff;
    border-bottom: 1px solid #e5e5e5;
  }
  
  .navbar-toggler {
    border: none;
  }
  
  .navbar-nav .nav-link {
    color: black !important;
  }
  
  .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba%2810, 10, 10, 0.7%29' stroke-width='2' linecap='round' linejoin='round' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
  }
  
  .navbar-logo {
    height: 80px;
    width: 160px;
  }
  
  @media (min-width: 992px) {
    .sidebar {
      display: none;
    }
  }
  
  @media (max-width: 767px) {
    .navbar-logo {
      height: 80px !important;
      width: 120px !important;
    }
  }
  
  .sidebar {
    position: fixed;
    top: 0;
    right: -250px;
    width: 250px;
    height: 100%;
    background-color: #fff;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5);
    transition: right 0.3s ease;
    z-index: 1050;
    padding: 20px;
  }
  
  .sidebar.open {
    right: 0;
  }
  
  .sidebar .btn-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
  }
  
  .sidebar .navbar-nav {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .sidebar .nav-link {
    margin: 10px 0;
    padding: 10px 0;
  }
  