.newOne {
    background-color: #D6CCB6;
    position: sticky;
    top: 0;
    z-index: 2;
    transition: all 0.5s ease-in-out;
    height: 700px;
}

.main-text {
    margin-top: 130px;
    padding: 20px;
}

@media (max-width: 991px) {
    .main-text {
        margin-top: 50px;
        padding: 15px;
    }
    .newOne{
        height: 780px;
    }
}

@media (max-width: 767px) {
    .main-text {
        margin-top: 30px;
        padding: 10px;
        text-align: center;
    }
}

.btn-enquiry {
    background: transparent;
    width: 200px;
    border: none;
    border-bottom: 3px solid black;
    cursor: pointer;
}

.img-fluid {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
}
