.description {
  background-color: #2c2618;
}
.desc {
  padding: 60px;
  color: white;
}
.drive-text {
  font-size: 50px;
}

.video-box {
  margin-bottom: 20px;
}

.desc p {
  padding-top: 20px;
}
.btn-readmore {
  background: transparent;
  border-bottom: 1px solid white;
  width: 130px;
  color: white;
}

@media (max-width: 767px) {
  .desc {
    padding: 10px !important;
  }
  .drive-text {
    font-size: 30px !important;
  }
  .video-box {
    padding: 0;
    width: 100%;
    margin-bottom: 20px;
  }
  .paragraph {
    margin-bottom: 20px !important;
  }
}
