.services{
    background-color: #D6CCB6;
}
.expert-services {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    font-weight: bold;
}

.service-list {
    list-style: none;
    padding: 0;
}

.service-list li {
    margin-bottom: 1.5rem;
}

.service-list h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    font-weight: bold;
}

.service-list p {
    font-size: 1rem;
    margin: 0;
}

.car-image {
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
}
.service-list{
    margin-left: 140px;
    margin-top: 80px;
}
@media (max-width: 991px) {
   .service-list{
    margin-left: 30px;
    margin-top: 20px;
   }
   .expert-services {
    font-size: 20px;
   }
   .brief-title{
    font-size: 20px !important;
   }
   .brief-desc{
    font-size: 15px;
   }
}
