.footer {
    background-color: #1a1a1a;
    color: #ccc;
    padding: 40px 20px;
}

.footer-logo {
    width: 300px;
   
}

.footer-heading {
    font-size: 1.2rem;
    margin-bottom: 15px;
    color: #fff;
}

.footer-text {
    font-size: 0.9rem;
    color: #ccc;
}

.footer-list {
    list-style: none;
    padding: 0;
}

.footer-list li {
    margin-bottom: 10px;
}

.footer-list li a {
    color: #ccc;
    text-decoration: none;
    transition: color 0.2s;
}

.footer-list li a:hover {
    color: #fff;
}

.footer-bottom {
    text-align: center;
    margin-top: 20px;
    border-top: 1px solid #333;
    padding-top: 10px;
}

@media (max-width: 767px) {
    .footer {
        text-align: center;
    }
   .footer-heading{
      font-size: larger;
   }
    .footer-logo {
        margin-bottom: 20px;
    }
}
