.hero-section {
  position: relative;
  width: 100%;
  height: 600px;
  overflow: hidden;
}

.video-section {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.video-background {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%);
  z-index: -1; 
  opacity: 0.5; 
}

@media (max-width: 768px) {
  .hero-section {
      height: 250px; 
  }
}
