
.slide-cars {
  width: 100%;
  height: auto;
  overflow: hidden;
  padding: 0;
  margin: 0;
  position: relative;
}

.slide {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.slide-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
  padding: 0;
  margin: 0;
}

@media (max-width: 768px) {
  .slide-cars {
    height: auto;
    padding: 0;
    margin: 0;
  }
  .slide-image {
    width: 100%;
    height: auto;
    object-fit: cover;
    padding: 0;
    margin: 0;
  }
}
